.skills-container {
	margin: 4rem 0;
	position: relative;
}

.skills-container {
	font-size: 1.5rem;
	font-weight: 600;
	margin-bottom: 3.5rem;
}

.skills-container::after,
.skills-container::before {
	content: " ";
	width: 28rem;
	height: 28rem;
	border-radius: 28.125rem;
	background: #7c5fe6; /*background light*/
	position: absolute;
	z-index: -1;
	top: 4rem;
	/* left: 0rem; */

	filter: blur(40px);
}

.skill-container::after {
	top: -3rem;
	left: -5rem;
}

.skills-container::before {
	background: #9e51b9;
	bottom: 0rem;
	right: -5rem;
}
.skills-container h5 {
	margin-bottom: 4rem;
	font-weight: 600;
	font-size: 1.5rem;
}

.skills-content {
	display: flex;
	align-items: flex-start;
	gap: 3rem;
}

.skills {
	flex: 1;
	display: grid;
	grid-gap: 3rem;
	grid-template-columns: repeat(2, 1fr);
}

.skills-info {
	flex: 1;
}

@media (max-width: 1025px) {
	.skills {
		grid-template-columns: repeat(1, 1fr);
		padding-left: 1rem;
	}

	.skills,
	.skills-content {
		align-items: center;
		grid-gap: 2rem;
	}
}

@media (max-width: 768px) {
	.skills-content {
		flex-direction: column-reverse;
		gap: 3rem;
	}

	.skills,
	.skills-info {
		width: 100%;
	}

	.skills-container h5 {
		font-size: 1.3rem;
		margin-bottom: 2rem;
	}

	.skills-container::after,
	.skills-container::before {
		width: 18rem;
		height: 18rem;
	}
}

@media (max-width: 600px) {
	.skills-container {
		padding: 0;
	}
}
